import Vue from 'vue'
import VueRouter from 'vue-router'

import home from '../components/main.vue'
import page from '../components/page.vue'
import content from '../components/content.vue'



Vue.use(VueRouter)

export default new VueRouter({
    routes: [
        {
            path: '/',
            name: 'home',
            component: home
        },
        {
            path: '/page',
            name: 'page',
            component: page
        },
        {
            path: '/content/:id',
            name: 'content',
            component: content
        }
    ]
})